
.product {
    background: #FFF;
    margin-bottom: 25px;
    position: relative;
    overflow: hidden;
    padding: 15px;
    height: 366px;
    max-height: 366px;
    min-height: 366px;
    -webkit-transition: all .2s ease-in;
    transition: all .2s ease-in;
}
.col-lg-3 .col-md-4 .col-sm-6 .col-xs-12{
    position: relative;
    min-height: 1px;
    padding-left: 10px;
    padding-right: 10px;
}
.product .product-description,
  .product .product-action {
    -webkit-transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
    transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
    transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform;
    transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform, 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) -webkit-transform;
}
.product:hover .product-description {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
.product:hover .product-action {
   -webkit-transform: none;
           transform: none;
}
.product-action {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
.product-action button {
    border-radius: 0;
    width: 100%;
    display: block;
}
.product-link {
    display: block;
    color: #3D3D3D;
}
.product-link:hover {
    color: #3D3D3D;
    text-decoration: none;
}
.product .product-image {
    width:100%;
    height:100%;
}
.product-image img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    /* height: auto;
    width:auto; */
    max-width: 100%;
    max-height: 100%;   
     /* height: 100%; */
}
.product-image-container{
    height: 225px;
    /* overflow: hidden; */
}
.product-description {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 20px 0px 15px;
    background: #FFF;
}
.product-info {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.product-description small {
    color: #808080;
}
.product-description h4 {
    font-size: 16px; 
    margin: 3px 0 5px 0;
    overflow: hidden;
    height: 20px;
    line-height: 1.3;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    word-wrap: break-word;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
}
.product-price-cart {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    /* justify-content: flex-end; */
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-size: 20px;
    font-weight: bold;
    color: #51D2B7;
}
.product-price-cart p {
    -webkit-box-flex: 2;
        -ms-flex-positive: 2;
            flex-grow: 2;
    font-size: 20px;
    font-weight: bold;
}

/* List grid */
.list-group-item {
        border: none;
        float: none;
        width: 100%;
        background-color: #fff;
        margin-bottom: 10px;
}
.list-group-item .product-image img{
    float: left;
}
.list-group-item  .product-image img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-height: 125px;
}
.list-group-item.col-lg-3.col-md-4.col-sm-6.col-xs-12{
    padding-left: 0;
    padding-right: 0;
}
.list-group-item .product-description {
    padding-top: 30px;
}
.list-group-item  .product-description small {
    color: #808080;
}
.list-group-item  .product-description h3 {
    margin: 5px 0 30px 0;
}
.list-group-item  .product-price-cart {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-top: 30px;
}



