
.product-details {
  border-bottom: 2px solid #F5F5F5;
  padding: 30px 0;
}
.product-details-image {
}
.product-details-description {
  padding-top: 30px;
}
.product-details-description small {
  color: #808080;
}
.product-details-description h3 {
  margin: 5px 0 30px 0;
}
.product-details-price-cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 30px;
}
.product-details-price-cart p {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
  color: #51D2B7;
}



